@import "https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap";
:root {
  --large-container: 1200px;
  --body-color: #fff;
  --basic-font: 16px, sans-serif;
}

* {
  box-sizing: border-box;
}

html, body {
  font: var(--basic-font);
  width: 100%;
  min-height: 100vh;
  background: var(--body-color);
  flex-flow: column;
  margin: 0;
  padding: 0;
  display: flex;
}

.main {
  flex: 1 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #000;
  text-decoration: none;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: normal;
}

.container {
  width: var(--large-container);
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (max-width: 1199px) {
  .container {
    width: 984px;
    padding: 0 20px;
  }
}

@media screen and (max-width: 1023px) {
  .container {
    width: 100%;
    padding: 0 20px;
  }
}

.button {
  width: 370px;
  height: 80px;
  border: rgba(0, 0, 0, 0);
  display: block;
}

.button--primary {
  color: #fff;
  text-transform: uppercase;
  background-color: #ff4343;
  font-family: Raleway, sans-serif;
  font-size: 1.5rem;
  font-weight: 900;
  transition: background-color .3s ease-in-out;
}

.button--primary:hover, .button--primary:active {
  cursor: pointer;
  background-color: #972b2b;
}

.header {
  width: 100%;
  position: absolute;
}

.header .container {
  border-bottom: 1px solid #babbc5;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 1023px) {
  .header .container {
    border-bottom: none;
    padding: 20px;
  }
}

.header__button .section__btn-icon path, .header__button:hover .section__btn-icon path, .header__button:active .section__btn-icon path {
  stroke: #fff;
}

.header__logo {
  width: 180px;
  height: 26px;
  display: flex;
}

@media screen and (max-width: 1023px) {
  .header__logo {
    width: 105px;
    height: 15px;
  }
}

.header__logo-part {
  max-width: 100%;
  max-height: 100%;
}

.header__burger-btn {
  width: 24px;
  height: 18px;
  background: none;
  border: none;
  outline: none;
  display: none;
}

@media screen and (max-width: 1023px) {
  .header__burger-btn {
    display: block;
  }

  .header__burger-btn:active .header__burger-icon path {
    fill: #d7d3d3;
  }
}

.header__burger-icon path {
  fill: #fff;
}

.nav__list {
  display: flex;
}

.nav__item {
  padding: 35px;
  transition: border-bottom .3s ease-in-out;
}

.nav__item.active {
  border-bottom: 2px solid #babbc5;
}

.nav__item:hover {
  cursor: pointer;
  border-bottom: 2px solid #babbc5;
}

.nav__item:active {
  border-bottom: 2px solid #ff4343;
}

.nav__item:active .nav__link {
  color: #ff4343;
}

.nav__link {
  color: #fff;
  text-transform: uppercase;
  font-family: Raleway, sans-serif;
  font-size: .8125rem;
  font-weight: 700;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

@media screen and (max-width: 1199px) {
  .nav__link {
    font-size: .75rem;
  }
}

@media screen and (max-width: 1023px) {
  .nav {
    display: none;
  }
}

.banner-section {
  height: 100vh;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(rgba(37, 43, 73, .5), rgba(37, 43, 73, .5)), url("home-page-bg.3f46cf86.jpg");
  background-position: center;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  align-items: center;
  display: flex;
}

.banner-section .container {
  padding: 230px 15px 120px;
}

@media screen and (max-width: 1199px) {
  .banner-section .container {
    padding: 260px 15px 125px;
  }
}

@media screen and (max-width: 1023px) {
  .banner-section .container {
    padding: 105px 20px 50px;
  }
}

.banner-section__content--wrapper {
  color: #fff;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.banner-section__title {
  text-transform: uppercase;
  margin-bottom: 30px;
  font-family: Raleway, sans-serif;
  font-size: 4rem;
  font-weight: 900;
