}

@media screen and (max-width: 1199px) {
  .banner-section__title {
    margin-bottom: 62px;
    font-size: 3rem;
    line-height: 3.5rem;
  }
}

@media screen and (max-width: 1023px) {
  .banner-section__title {
    margin-bottom: 32px;
    font-size: 2.25rem;
    line-height: 2.625rem;
  }
}

.banner-section__descr {
  margin-bottom: 80px;
  font-family: Raleway, sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.75rem;
}

@media screen and (max-width: 1199px) {
  .banner-section__descr {
    margin-bottom: 92px;
    font-size: 1.125rem;
    line-height: 1.3125rem;
  }
}

@media screen and (max-width: 1023px) {
  .banner-section__descr {
    margin-bottom: 50px;
    font-size: .875rem;
    line-height: 1rem;
  }
}

.banner-section__find-out-btn {
  margin: 0 auto;
}

@media screen and (max-width: 1023px) {
  .banner-section__find-out-btn {
    width: 100%;
    max-width: 280px;
    height: 58px;
  }
}

